.slider-control-centerleft{
    position: absolute;
    top: 50%;
    left: 0px;
    transform: translateY(-50%);
}

.slider-control-buttonleft{
    border: 0px;
    background: rgba(0, 0, 0, 0.4);
    color: white;
    padding: 10px;
    opacity: 1;
    cursor: pointer;
}


.slider-control-centerright{
    position: absolute;
    top: 50%;
    right: 0px;
    transform: translateY(-50%);
}

.slider-control-buttonright{
    border: 0px;
    background: rgba(0, 0, 0, 0.4);
    color: white;
    padding: 10px;
    opacity: 1;
    cursor: pointer;
}

.title{
    font-size: 54px; 
    color: white; 
    font-family: 'proxima-nova', sans-serif;
    margin-bottom: 0px !important
}

@media (max-width:500px)
{
    .button-group.js-radio-button-group {
        margin-left: 5%;
    }
    .mtext{
        font-size: 17px !important;
        margin: 0% !important
    }
    .title{
        font-size: 45px; 
     
        color: white; 
        font-family: 'proxima-nova', sans-serif;
        text-align: center;
    }
    .miniMap {
        position: relative !important;
        width: 100% !important;
        height: 240px !important;
      }
    .social-icons3 {
        padding-right: 13%
    }
    .slider-control-bottomcenter{
        display: none
    }

    .slider-control-centerleft{
        position: absolute;
        top: 25%;
        left: 0px;
        transform: translateY(-50%);
    }

    .slider-control-buttonleft{
        border: 0px;
        background: rgba(0, 0, 0, 0.4);
        color: white;
        padding: 6px;
        opacity: 1;
        cursor: pointer;
    }

    .slider-control-centerright{
        position: absolute;
        top: 25%;
        right: 0px;
        transform: translateY(-50%);
    }

    .slider-control-buttonright{
        border: 0px;
        background: rgba(0, 0, 0, 0.4);
        color: white;
        padding: 6px;
        opacity: 1;
        cursor: pointer;
    }

}


