div.wrapper {
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: left;
}

.row2 {
    color: #000000;
    /* background-color: #FFFFFF; */
}

#header, #container, #footer {
    display: block;
    width: 100%;
    margin: 0 auto;
}


#container {
    padding: 30px 0;
}
#about-us {
    display: block;
    width: 100%;
    line-height: 1.6em;
}

#about-us #about-intro {
    margin-bottom: 50px;
}

#about-us h2 {
    margin-bottom: 50px;
    padding-bottom: 40px;
    font-size: 45px;
    /* text-align: center; */
    text-transform: uppercase;
    border-bottom: 1px solid #CCCCCC;
    font-family: lato;
    font-weight: normal;
}

#about-intro img {
    margin: 0;
    padding-right: 23%;
    float: right;
    border: none;
    line-height: normal;
    vertical-align: middle;
    width: 45%;
}

#about-us #about-intro p {
    margin: 20px 0 0 0;
    padding: 0;
    /* text-align: center */
    font-family: lato;
    font-size: 17px;
}

#about-us #team {
    margin-bottom: 0;
}

#about-us #team ul li {
    margin-bottom: 30px;
}

@media (max-width:500px)
{
    #about-intro img {
        margin: 0;
        padding-right: 23%;
        float: right;
        border: none;
        line-height: normal;
        vertical-align: middle;
        width: 75%;
    }
    #about-us h2 {
        margin-bottom: 50px;
        padding-bottom: 40px;
        font-size: 39px;
        text-align: center;
        text-transform: uppercase;
        border-bottom: 1px solid #CCCCCC;
    }

    p{
        padding: 24px !important
    }
    
}