.imagedropshadowbrand {
    padding: 5px;
    border: solid 1px #a5a5a5;
    width: 305px
}
.imagedropshadowbrando {
    padding: 4px !important;
    border: solid 1px #a5a5a5;
    width: 305px;
    opacity: 0.8;
}
a:hover img.imagedropshadowbrand {
    border: solid 1px #CCC;
    -moz-box-shadow: 1px 1px 5px #999;
    -webkit-box-shadow: 1px 1px 5px #999;
        box-shadow: 1px 1px 5px #999;
}

.brandgrid{
    margin-left: 0px !important;
    margin-bottom: 30px !important;
    width: 1325px;
    height: 100%
}


.ui-group {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 40px;
   
  }
  
  /* ---- button ---- */
  
  .button {
    display: inline-block;
    outline: none;
    padding: 0.5em 1.0em;
    margin-top:6px;
    border: 1px solid #ccc;
    border-radius: 4px;
    color: #444;
    font-size: 0.9em;
    background-color: white;
    transition: all 0.2s;
  }
  
  .button:hover {
    background-color: #ccc;
    color: black;
  }
  
  .button:active,
  .button.is-checked {
    background-color: #444;
    border:1px solid #444;
  }
  
  .button.is-checked {
    color: white;
  }
  
  .button:active {
  }
  
  /* ---- button-group ---- */
  
  .button-group:after {
    content: '';
    display: block;
    clear: both;
  }
  
  .button-group .button {
    float: left;
    border-radius: 0;
    margin-left: 0;
    margin-right: -1px;
  }
  
  .button-group .button:first-child { border-radius: 4px 0 0 4px; }
  .button-group .button:last-child { border-radius: 0 4px 4px 0; }
  
  
  
 

@media (max-width:800px)
{
    .imagedropshadowbrand {
        padding: 5px;
        border: solid 1px #a5a5a5;
        width: 300px
    }
}

@media (max-width:500px)
{
    

    .brandgrid{
        margin-left: 11% !important;
        margin-bottom: 30px !important;
        width: 1089px;
        height: 100%
    }
}